export default {
    data(){
        return{
            user:{},
            loading:true
        }
    },
    async asyncData(context){
        const user = await context.$axios.$get(`https://60ce519991cc8e00178dcda8.mockapi.io/orders/${context.params.id}`)
        return{ user }
    },
    methods:{
        showUser(){;
          this.loading = false;
        }
    },
    created(){
        this.showUser()
    }
// name:"_id"
}
